<template>
  <div class="acceptManage">
    <Crumbs
      :parent-manage="'验收管理'"
      :children-manage="'验收管理'"
      @isShowScreen="isShowScreen"
    ></Crumbs>

    <Content
      v-if="isScreen"
      class="content screenStyle"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="searchData"
        :label-width="65"
      >
        <Row type="flex" justify="space-between">
          <Col
            span="7"
          >
          <FormItem label="主管单位">
            <Select
              v-model="searchData.basicsOrganizationId"
              clearable
              filterable
              placeholder="请选择主管单位"
              transfer
            >
              <Option
                v-for="item in orgList"
                :key="item.orgId"
                :value="item.orgId"
              >{{ item.orgName }}</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            span="7"
          >
          <FormItem label="承担单位">
            <Select
              v-model="searchData.basicsOrganizationUnderTakeId"
              clearable
              filterable
              placeholder="请选择承担单位"
              transfer
            >
              <Option
                v-for="item in orgList"
                :key="item.orgId"
                :value="item.orgId"
              >{{ item.orgName }}</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            span="7"
          >
          <FormItem label="资金类别">
            <Select
              v-model="searchData.basicsCapitalId"
              clearable
              filterable
              placeholder="请选择资金类别"
              transfer
            >
              <Option
                v-for="item in capitalList"
                :key="item.capitalId"
                :value="item.capitalId"
              >{{ item.capitalName }}</Option>
            </Select>
          </FormItem>
          </Col>
        </Row>
        <Row style="margin-top:15px;" type="flex" justify="space-between">
          <Col
            span="7"
          >
          <FormItem label="验收类别">
            <Select
              v-model="searchData.acceptanceType"
              clearable
              filterable
              placeholder="请选择验收类别"
              transfer
            >
              <Option value="区级">区级</Option>
              <Option value="市级">市级</Option>
              <Option value="自验">自验</Option>
            </Select>
          </FormItem>
          </Col>
          <Col
            span="7"
          >
          <FormItem label="验收时间">
            <DatePicker
              v-model="searchData.acceptanceTime"
              style="width:100%;"
              type="date"
              transfer
              placeholder="请选择验收时间"
              placement="bottom-start"
            ></DatePicker>
          </FormItem>
          </Col>
          <Col
            span="7"
            style="text-align:right"
          >
          <Button type="primary" @click="searchList">
            确认
          </Button>
          </Col>
        </Row>
      </Form>
    </Content>
    <img id="showPic" style="display:none">
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button
        type="primary"
        icon="md-add"
        @click="handleAdd"
      >
        新增
      </Button>
      <Button
        type="primary"
        icon="ios-create"
        @click="handleModify"
      >
        修改
      </Button>
      <Button
        type="primary"
        icon="md-close"
        @click="handleDelete"
      >
        删除
      </Button>
      <Button icon="md-print" @click.stop="printContent(23)">
        打印
      </Button>
      <Button icon="md-cloud-download" @click="downloadQR()">
        条形码打印
      </Button>
      <div class="quickSearch">
        <strong>快速查询：</strong>
        <Input
          v-model="archivesNumber"
          style="width:300px"
          placeholder="档案编码"
          @input="searchListByCode"
        ></Input>
      </div>
      <Table
        ref="table"
        class="table"
        highlight-row
        :columns="columns"
        :data="acceptList"
        @on-row-click="rowClick"
      ></Table>
      <!-- 分页组件 -->
      <Page
        style="text-align: right"
        :total="total"
        :current="searchData.pageNo"
        :page-size="searchData.pageSize"
        show-sizer
        transfer
        @on-change="pageNoChange"
        @on-page-size-change="pageSizeChange"
      />
    </Content>
    <!-- 验收管理弹窗 -->
    <acceptModal ref="acceptModal" :is-create="isCreate" :capital-list="capitalList" :org-list="orgList" :is-disabled="isDisabled" @refreshList="searchList" />
  </div>
</template>

<script>
import { getLodop } from '@/common/lodop.js'
import acceptModal from './components/acceptManageModel.vue';
import Crumbs from '../components/Crumbs';
import axiosUtils from '@/common/axiosUtils.js'
import JsBarcode from 'jsbarcode'
export default {
  name: 'AcceptManage',
  components: { Crumbs, acceptModal },
  data() {
    return {
      archivesNumber: null,
      // 单选框表格
      showSelect: false,
      // 控制筛选框显示隐藏  默认隐藏
      isScreen: false,
      // 筛选内容_字段
      searchData: {
        pageNo: 1,
        pageSize: 10,
        acceptanceTime: null,
        acceptanceType: null,
        basicsCapitalId: null,
        basicsOrganizationUnderTakeId: null,
        basicsOrganizationId: null
      },
      isCreate: false,
      total: 0,
      // 表格内容 字段相对应
      acceptList: [],
      // 控制 是否禁用  默认不禁用
      isDisabled: false,
      columns: [
        {
          type: 'selection',
          width: 60,
          align: 'center',
          fixed: 'left'
        },
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          indexMethod: this.indexMethod
        },
        {
          title: '项目名称',
          key: 'projectName',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.showInfo(params.row.id)
                }
              }
            }, params.row.projectName);
          }
        },
        {
          title: '主管单位',
          key: 'basicsOrganization',
          render: (h, params) => {
            const org = params.row.basicsOrganization
            return h('div', org ? org.basicsOrganizationName : '');
          }
        },
        {
          title: '承担单位',
          key: 'basicsOrganization',
          render: (h, params) => {
            const org = params.row.basicsOrganizationUnderTake
            return h('div', org ? org.basicsOrganizationName : '');
          }
        },
        {
          title: '资金类别',
          key: 'basicsCapital',
          render: (h, params) => {
            const capital = params.row.basicsCapital
            return h('div', capital ? capital.basicsCapitalName : '');
          }
        },
        {
          title: '验收类别',
          key: 'acceptanceType'
        },
        {
          title: '建设位置',
          key: 'constructionLocation'
        },
        {
          title: '建设内容',
          key: 'constructionContents'
        },
        {
          title: '检验结果',
          key: 'inspectionCompleted'
        },
        {
          title: '档案编码',
          key: 'archivesNumber',
          width: 240,
          render: (h, params) => {
            return h('img', {
              attrs: {
                id: 'code' + params.row.id,
                style: 'width: 220px;height: 70px;'
              }
            })
          }
        },
        {
          title: '验收时间',
          key: 'acceptanceTime',
          render: (h, params) => {
            const time = params.row.acceptanceTime
            return h('div', time ? this.$moment(params.row.acceptanceTime).format('YYYY年MM月DD日') : '');
          }
        }
      ],
      capitalList: [],
      orgList: [],
      allList: {}
    }
  },
  mounted() {
    // 这里总条数和数据 分成了两个api
    this.searchList();
    this.getListTotal();
    // 搜索框的list
    this.getOrganizeData();
    this.getFundData()
  },
  methods: {
    getStartIndex(allCount, pageNo, pageSize) {
      const currentPageNo = Math.ceil(allCount / pageSize);
      pageNo = (pageNo > currentPageNo) ? currentPageNo : pageNo;
      const startIndex = (pageNo - 1) * pageSize;
      return startIndex;
    },
    // 通过档案编码 查 列表
    searchListByCode() {
      const archivesNumber = this.archivesNumber
      if (archivesNumber) {
        const list = this.allList.data
        if (list !== null && list.length !== 0) {
          const result = list.filter(item => {
            return item.archivesNumber.indexOf(archivesNumber) !== -1
          })
          // 清空
          // this.$set(this, 'acceptList', [])
          const pageNo = this.searchData.pageNo
          const pageSize = this.searchData.pageSize
          const len = result.length
          const startIndex = this.getStartIndex(len, pageNo, pageSize)
          const pageCount = pageNo * pageSize
          const endIndex = (len > pageCount) ? pageCount - 1 : (len === 1 ? 1 : len - 1)
          const acceptList = result.slice(startIndex, endIndex)
          this.$set(this, 'acceptList', acceptList)
          this.$set(this, 'total', len)
          this.$nextTick(() => {
            // 生成条形码
            for (const item of acceptList) {
              if (item.archivesNumber) {
                JsBarcode('#code' + item.id, item.archivesNumber, {
                  format: 'CODE128', //  选择要使用的条形码类型
                  text: item.code,
                  height: 70,
                  displayValue: true, // 是否在条形码下方显示文字
                  textPosition: 'bottom'// 设置文本的垂直位置
                })
              }
            }
          })
        }
      } else {
        this.searchList();
        this.getListTotal();
      }
    },
    // 查全部列表
    getAllList() {
      const that = this;
      that.$Spin.show();
      axiosUtils.ajaxPostUtil('/selectAcceptanceForm', { pageNo: 1, pageSize: 1000000 }, (res) => {
        that.$Spin.hide();
        if (res.ok) {
          const data = res.data
          that.allList = {
            data: data,
            total: data.length
          }
        } else {
          that.$Message.error(res.message);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    printContent(type) {
      const that = this
      const selectRows = this.$refs.table.getSelection()
      if (selectRows.length === 0) {
        that.$Message.warning('您未选择内容');
        return
      }
      const ids = selectRows.map(item => {
        return item.id
      })
      const { href } = that.$router.resolve({
        path: '/printView',
        query: {
          title: that.$route.meta.title,
          type: type,
          ids: ids.join(','),
          isAllSelection: ids.length === that.acceptList.length ? 1 : 0
        }
      })
      window.open(href, '_blank')
    },
    rowClick(data, index) {
      this.$refs.table.toggleSelect(index);
    },
    getOrganizeData() {
      const that = this;
      that.$Spin.show();
      that.commonUtil.ajaxPostUtil('/organization/searchOrganization/list', {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.orgList = res.data.orgList
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 资金类别下拉框
    getFundData() {
      const that = this;
      that.$Spin.show();
      that.commonUtil.ajaxPostUtil('/capital/searchCapital/list', {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.capitalList = res.data.capitalList
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 二维码下载
    downloadQR() {
      const rows = this.$refs.table.getSelection()
      if (rows.length > 0) {
        const that = this;
        that.printQRCode(rows);
      } else {
        this.$Message.error('请选择行！！')
      }
    },
    // 打印
    printQRCode(list) {
      const LODOP = getLodop();
      if (typeof (LODOP) === 'undefined') {
        this.$Modal.confirm({
          title: '打印',
          content: '您未安装打印控件，是否跳转地址安装？',
          onOk: () => {
            window.open('http://www.lodop.net/demolist/CLodop_Setup_for_Win32NT.zip', '_blank');
          }
        });
      } else {
        LODOP.PRINT_INIT('');
        // LODOP.SET_PRINT_STYLE('FontName', '方正小标');
        // LODOP.SET_PRINT_STYLE('FontSize', 7);
        // 字体：方正小标
        // LODOP.SET_PRINT_PAGESIZE(0, '70mm', '36mm', '报表');
        for (const item of list) {
          LODOP.ADD_PRINT_TEXT('18.31mm', '1.1mm', '13.23mm', '3.97mm', '项目名称:');
          LODOP.SET_PRINT_STYLEA(0, 'FontName', '方正小标');
          LODOP.SET_PRINT_STYLEA(0, 'FontSize', 7);
          LODOP.ADD_PRINT_TEXT('18.31mm', '12.2mm', '62.80mm', '3.97mm', item.projectName);
          LODOP.SET_PRINT_STYLEA(0, 'FontName', '方正小标');
          LODOP.SET_PRINT_STYLEA(0, 'FontSize', 7);
          LODOP.ADD_PRINT_TEXT('21.75mm', '1.1mm', '13.23mm', '3.97mm', '建设单位:');
          LODOP.SET_PRINT_STYLEA(0, 'FontName', '方正小标');
          LODOP.SET_PRINT_STYLEA(0, 'FontSize', 7);
          LODOP.ADD_PRINT_TEXT('21.75mm', '12.2mm', '62.80mm', '3.97mm', item.basicsOrganizationUnderTake ? item.basicsOrganizationUnderTake.basicsOrganizationName : '');
          LODOP.SET_PRINT_STYLEA(0, 'FontName', '方正小标');
          LODOP.SET_PRINT_STYLEA(0, 'FontSize', 7);
          LODOP.ADD_PRINT_TEXT('25.19mm', '1.1mm', '13.23mm', '3.97mm', '建设地点:');
          LODOP.SET_PRINT_STYLEA(0, 'FontName', '方正小标');
          LODOP.SET_PRINT_STYLEA(0, 'FontSize', 7);
          LODOP.ADD_PRINT_TEXT('25.19mm', '12.2mm', '62.80mm', '3.97mm', item.constructionLocation);
          LODOP.SET_PRINT_STYLEA(0, 'FontName', '方正小标');
          LODOP.SET_PRINT_STYLEA(0, 'FontSize', 7);
          LODOP.ADD_PRINT_TEXT('28.63mm', '1.1mm', '13.23mm', '3.97mm', '建设内容:');
          LODOP.SET_PRINT_STYLEA(0, 'FontName', '方正小标');
          LODOP.SET_PRINT_STYLEA(0, 'FontSize', 7);
          LODOP.ADD_PRINT_TEXT('28.63mm', '12.2mm', '62.80mm', '3.97mm', item.constructionContents);
          LODOP.SET_PRINT_STYLEA(0, 'FontName', '方正小标');
          LODOP.SET_PRINT_STYLEA(0, 'FontSize', 7);
          LODOP.ADD_PRINT_BARCODE('1.22mm', '1.06mm', '67.88mm', '15.5mm', '128A', item.archivesNumber);
          LODOP.SET_PRINT_STYLEA(0, 'ShowBarText', 0);
          LODOP.SET_PRINT_STYLEA(0, 'Horient', 3);
          LODOP.SET_PRINT_STYLEA(0, 'Bold', 1);
          // LODOP.ADD_PRINT_BARCODE('1.95mm', '1.1mm', '70.7mm', '15.5mm', '128A', item.archivesNumber);
          // LODOP.SET_PRINT_STYLEA(0, 'PDF417ErrorLevel', 0);
          // LODOP.SET_PRINT_STYLEA(0, 'DataCharset', 'UTF-8');
          // LODOP.SET_PRINT_STYLEA(0, 'AngleOfPageInside', -90);
          LODOP.SET_PRINT_MODE('FULL_HEIGHT_FOR_OVERFLOW', true); // 高度缩放
          // LODOP.SET_PRINT_MODE('PRINT_PAGE_PERCENT', 'Full-Page');// 整页缩放
          // LODOP.ADD_PRINT_HTM('0mm', '0mm', 'RightMargin:0cm', 'BottomMargin:0mm', '<div style="width:100%;border:1px solid red;margin:0px auto">123</div>');
          LODOP.NewPage();// 创建新的页，实现多页打印
        }
        LODOP.PRINT_DESIGN();
        // LODOP.PREVIEW();
      }
    },
    // 自定义序号
    indexMethod(row) {
      const index = row._index
      return this.searchData.pageSize * (this.searchData.pageNo - 1) + index + 1;
    },
    handleAdd() {
      this.isCreate = true;
      this.isDisabled = false;
      this.$refs.acceptModal.showModal();
    },
    // 分页
    pageNoChange(pageNo) {
      this.searchData.pageNo = pageNo
      this.searchList()
    },
    pageSizeChange(pageSize) {
      this.searchData.pageSize = pageSize
      this.searchList()
    },
    searchList() {
      const that = this;
      const searchData = Object.assign({}, that.searchData)
      // 获取全部数据
      that.getAllList()
      that.$Spin.show();
      searchData.acceptanceTime = searchData.acceptanceTime || null
      axiosUtils.ajaxPostUtil('/selectAcceptanceForm', searchData, (res) => {
        that.$Spin.hide();
        if (res.ok) {
          const data = res.data
          that.acceptList = data
          that.$nextTick(() => {
            // 生成条形码
            for (const item of data) {
              if (item.archivesNumber) {
                JsBarcode('#code' + item.id, item.archivesNumber, {
                  format: 'CODE128', //  选择要使用的条形码类型
                  text: item.code,
                  height: 70,
                  displayValue: true, // 是否在条形码下方显示文字
                  textPosition: 'bottom'// 设置文本的垂直位置
                })
              }
            }
          })
        } else {
          that.$Message.error(res.message);
        }
      }, () => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    getListTotal() {
      const that = this;
      that.$Spin.show();
      axiosUtils.ajaxGetUtil('/selectPageSizeAll', {}, (res) => {
        that.$Spin.hide();
        if (res.ok) {
          that.total = res.data
        } else {
          that.$Message.error(res.message);
        }
      }, () => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 子组件接收 控制筛选内容的隐藏和显示
    isShowScreen(msg) {
      this.isScreen = msg;
    },
    // 更新操作
    handleModify() {
      const that = this;
      if (that.isCorrentRow()) {
        that.$nextTick(() => {
          that.isDisabled = false
          that.isCreate = false;
          that.$refs.acceptModal.showModal((that.$refs.table.getSelection())[0].id);
        })
      }
    },
    showInfo(id) {
      this.$nextTick(() => {
        this.isDisabled = true
        this.$refs.acceptModal.showModal(id);
      })
    },
    isCorrentRow() {
      const that = this;
      const selectRows = this.$refs.table.getSelection()
      if (selectRows.length === 0) {
        that.$Message.warning('您未选择内容');
        return false
      } else if (selectRows.length > 1) {
        that.$Message.warning('请选中一行！');
        return false
      }
      return true;
    },
    // 删除
    handleDelete() {
      const that = this;
      const selectRows = this.$refs.table.getSelection()
      if (selectRows.length > 0) {
        that.$Modal.confirm({
          title: '删除',
          content: '确认删除此数据吗？',
          onOk: () => {
            that.$Spin.show({
              render: (h) => {
                return h('div', [
                  h('Icon', {
                    'class': 'demo-spin-icon-load',
                    props: {
                      type: 'ios-loading',
                      size: 18
                    }
                  }),
                  h('div', '加载中')
                ])
              }
            });
            const ids = selectRows.map(item => {
              return item.id
            }).join(',')
            axiosUtils.ajaxPostUtil('/deleteAcceptanceForm?acceptanceFormId=' + ids, {}, (res) => {
              that.$Spin.hide();
              if (res.ok) {
                that.$Message.success(res.message);
                that.searchList();
              } else {
                that.$Message.error(res.message);
              }
            }, () => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
        });
      } else {
        that.$Message.warning('请选择删除行！');
      }
    }
  }
}
</script>
<style scoped>
  .quickSearch{
    display: flex;
    justify-content: space-between;
    white-space: nowrap;
    align-items: center;
    float: right;
  }
</style>



