<template>
  <div>
    <div v-if="!isDisabled" style="margin-bottom:10px;">
      <b>
        <Icon
          size="16"
          type="md-alert"
          style="margin-right:10px;"
        />请上传与设置信息一致的附件或证明性文件，本系统只接受jpg、png格式文件，请上传前进行确认。</b>
    </div>
    <div v-for="(item,key) in uploadList" :key="key" class="demo-upload-list">
      <template v-if="item.status === 'finished'">
        <img :src="item.url">
        <div class="demo-upload-list-cover">
          <Icon type="ios-eye-outline" @click.native="handleView(item.url)"></Icon>
          <Icon v-if="!isDisabled" type="ios-trash-outline" @click.native="handleRemove(key,item.id)"></Icon>
        </div>
      </template>
      <template v-else>
        <Progress v-if="item.showProgress" :percent="item.percentage" hide-info></Progress>
      </template>
    </div>
    <Upload
      v-if="!isDisabled"
      ref="upload"
      :show-upload-list="false"
      :default-file-list="defaultList"
      :on-success="handleSuccess"
      :accept="'.jpg,.jpeg,.png'"
      :format="['jpg','jpeg','png']"
      :on-format-error="handleFormatError"
      :before-upload="handleBeforeUpload"
      type="drag"
      action="http://api.tjjnht.cn/api/uploadFile"
      style="display: inline-block;width:58px;"
    >
      <div style="width: 58px;height:58px;line-height: 58px;">
        <Icon type="ios-cloud-upload-outline" size="20"></Icon>
      </div>
    </Upload>
    <Modal v-model="visible" title="查看大图">
      <img v-if="visible" :src="imgPath" style="width: 100%">
    </Modal>
  </div>
</template>
<script>
import axiosUtils from '@/common/axiosUtils.js'
export default {
  props: {
    isDisabled: {
      type: Boolean,
      default: false
    },
    isCreate: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      defaultList: [],
      imgPath: '',
      visible: false,
      uploadList: []
    }
  },
  methods: {
    // 编辑图像记录时
    setImgRecord(pictures) {
      this.uploadList = pictures.map(item => {
        return {
          id: item.id,
          name: item.name,
          url: item.pictureUrl,
          status: 'finished'
        }
      })
    },
    clearImgRecord() {
      // this.$refs.upload.clearFiles()
      this.uploadList = []
    },
    handleView(url) {
      this.imgPath = url
      this.visible = true;
    },
    handleRemove(index, id) {
      const that = this;
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此图片吗？',
        onOk: () => {
          if (that.isCreate) {
            that.uploadList.splice(index, 1);
            return;
          }
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          axiosUtils.ajaxPostUtil('/deleteAcceptancePicture?pictureId=' + id, {}, (res) => {
            that.$Spin.hide();
            if (res.ok) {
              that.uploadList.splice(index, 1);
            } else {
              that.$Message.error(res.message);
            }
          }, () => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        }
      });
    },
    handleSuccess(res, file) {
      this.$Spin.hide();
      if (res.ok) {
        this.uploadList.push({
          id: res.id,
          name: file.name,
          url: res.data,
          status: file.status,
          showProgress: file.showProgress,
          percentage: file.percentage
        })
        this.$Message.success(res.message);
      } else {
        this.$Message.error(res.message);
      }
    },
    handleFormatError(file) {
      this.$Spin.hide();
      this.$Notice.warning({
        title: '文件格式不正确',
        desc: file.name + '格式不正确, 请选择 jpg 或 png后缀格式.'
      });
    },
    handleBeforeUpload() {
      this.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '正在上传图片中，请稍候......')
          ])
        }
      });
      return true;
    },
    getImgRecord() {
      const pictures = [...this.uploadList]
      return pictures.map(item => {
        return {
          pictureUrl: item.url
        }
      })
    }
  }
}
</script>
<style scoped>
    .demo-upload-list{
        display: inline-block;
        width: 60px;
        height: 60px;
        text-align: center;
        line-height: 60px;
        border: 1px solid transparent;
        border-radius: 4px;
        overflow: hidden;
        background: #fff;
        position: relative;
        box-shadow: 0 1px 1px rgba(0,0,0,.2);
        margin-right: 4px;
    }
    .demo-upload-list img{
        width: 100%;
        height: 100%;
    }
    .demo-upload-list-cover{
        display: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0,0,0,.6);
    }
    .demo-upload-list:hover .demo-upload-list-cover{
        display: block;
    }
    .demo-upload-list-cover i{
        color: #fff;
        font-size: 20px;
        cursor: pointer;
        margin: 0 2px;
    }
</style>



