
<template>
  <div>
    <Modal
      v-model="isPop"
      class="selStyle specialWidth"
      :mask-closable="false"
    >
      <p slot="header">
        <span v-if="isDisabled">验收管理信息</span>
        <span v-else>{{ isCreate?'添加':'修改' }}验收管理信息</span>
      </p>
      <Tabs
        :animated="false"
        class="innerTabStyle"
        value="name1"
      >
        <Form
          ref="form"
          :model="form"
          :rules="ruleValidateType"
          :label-width="85"
        >
          <TabPane
            label="基本信息"
            name="name1"
          >
            <Row>
              <Col span="12">
              <FormItem
                label="项目名称"
                prop="projectName"
              >
                <Select
                  v-model="form.projectName"
                  :disabled="isDisabled"
                  clearable
                  filterable
                  transfer
                >
                  <Option
                    v-for="item in projectList"
                    :key="item.projectId"
                    :value="item.projectName"
                  >{{ item.projectName }}</Option>
                </Select>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem label="主管单位" prop="basicsOrganizationId">
                <Select
                  v-model="form.basicsOrganizationId"
                  :disabled="isDisabled"
                  clearable
                  filterable
                  transfer
                >
                  <Option
                    v-for="item in orgList"
                    :key="item.orgId"
                    :value="item.orgId"
                  >{{ item.orgName }}</Option>
                </Select>
              </FormItem>
              </Col>

            </Row>
            <Row>
              <Col span="12">
              <FormItem label="承担单位" prop="basicsOrganizationUnderTakeId">
                <Select
                  v-model="form.basicsOrganizationUnderTakeId"
                  :disabled="isDisabled"
                  clearable
                  filterable
                  transfer
                >
                  <Option
                    v-for="item in orgList"
                    :key="item.orgId"
                    :value="item.orgId"
                  >{{ item.orgName }}</Option>
                </Select>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem label="资金类别" prop="basicsCapitalId">
                <Select
                  v-model="form.basicsCapitalId"
                  :disabled="isDisabled"
                  clearable
                  filterable
                  transfer
                >
                  <Option
                    v-for="item in capitalList"
                    :key="item.capitalId"
                    :value="item.capitalId"
                  >{{ item.capitalName }}</Option>
                </Select>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="验收类别"
                prop="acceptanceType"
              >
                <Select
                  v-model.trim="form.acceptanceType"
                  :disabled="isDisabled"
                  clearable
                  transfer
                  placeholder="请填写验收类别"
                >
                  <Option value="区级">区级</Option>
                  <Option value="市级">市级</Option>
                  <Option value="自验">自验</Option>
                </Select>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="建设位置"
                prop="constructionLocation"
              >
                <Input
                  v-model.trim="form.constructionLocation"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写建设位置"
                ></Input>
              </FormItem>
              </Col>

            </Row>
            <Row>
              <Col span="24">
              <FormItem
                label="建设内容"
                prop="constructionContents"
              >
                <Input
                  v-model.trim="form.constructionContents"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写建设内容"
                ></Input>
              </FormItem>
              </Col>
            </Row>
          </TabPane>
          <TabPane
            label="验收情况"
            name="name2"
          >
            <Row>
              <Col span="24">
              <FormItem
                label="检验结果"
              >
                <Select
                  v-model.trim="form.inspectionCompleted"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请选择检验结果"
                >
                  <Option value="合格">合格</Option>
                  <Option value="不合格">不合格</Option>
                </Select>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="24">
              <FormItem
                label="实际完成"
              >
                <Input v-model="form.actualCompletion" type="textarea" :disabled="isDisabled" :autosize="{minRows: 1,maxRows: 5}" placeholder="请填写实际完成"></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="24">
              <FormItem
                label="情况备注"
              >
                <Input v-model="form.remarks" type="textarea" :disabled="isDisabled" :autosize="{minRows: 1,maxRows: 5}" placeholder="请填写情况备注"></Input>
              </FormItem>
              </Col>
            </Row>
          </TabPane>
          <TabPane
            label="图像记录"
            name="name3"
          >
            <imgRecord ref="imgRecord" :is-disabled="isDisabled" :is-create="isCreate" />
          </TabPane>
          <TabPane
            label="附件"
            name="name7"
          >
            <uploadFile ref="uploadFile" flag :is-disabled="isDisabled" :form="form" :is-create="isCreate" />
          </TabPane>
        </Form>
      </Tabs>
      <p
        slot="footer"
        style="text-align: left"
      >
        <Button icon="md-print">打印</Button>
        <Button icon="md-exit">导出</Button>
        <Button
          v-if="!isDisabled"
          style="float: right;margin-left: 8px"
          @click="handleCancel('form')"
        >
          取消
        </Button>
        <Button
          v-if="!isDisabled"
          style="float: right"
          type="primary"
          @click="handleConfirm('form')"
        >
          确认
        </Button>
      </p>
    </Modal>
  </div>
</template>
<script>
import imgRecord from '@/components/imgRecord.vue'
import uploadFile from '@/components/fileUpload.vue'
import axiosUtils from '@/common/axiosUtils.js'
export default {
  name: 'AcceptManage',
  components: { uploadFile, imgRecord },
  props: {
    isDisabled: {
      type: Boolean,
      default: false
    },
    isCreate: {
      type: Boolean,
      default: false
    },
    capitalList: {
      type: Array,
      default: () => {
        return []
      }
    },
    orgList: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
      // 弹出框内容显示隐藏   默认隐藏
      isPop: false,
      // 添加表单的内容字段  ->  传递数据的字段
      form: {
        // 项目名称
        projectName: '',
        // 主管单位
        basicsOrganizationId: '',
        // 承担单位
        basicsOrganizationUnderTakeId: '',
        // 资金类别
        basicsCapitalId: '',
        // 验收类别
        acceptanceType: '',
        // 建设位置
        constructionLocation: '',
        // 建设内容
        constructionContents: '',
        //  检验结果
        inspectionCompleted: '',
        //  实际完成
        actualCompletion: '',
        // 情况备注
        remarks: '',
        enclosureList: [],
        pictureList: []
      },
      // 表单内容的校验规则
      ruleValidateType: {
        projectName: [
          { required: true, message: '请填写项目名称', trigger: 'blur' }
        ],
        basicsOrganizationId: [
          { type: 'number', required: true, message: '请选择主管单位', trigger: 'blur' }
        ],
        basicsOrganizationUnderTakeId: [
          { type: 'number', required: true, message: '请选择承担单位', trigger: 'blur' }
        ],
        basicsCapitalId: [
          { type: 'number', required: true, message: '请选择资金类别', trigger: 'blur' }
        ],
        constructionContents: [
          { required: true, message: '请填写建设内容', trigger: 'blur' }
        ],
        constructionLocation: [
          { required: true, message: '请填写建设位置', trigger: 'blur' }
        ],
        acceptanceType: [
          { required: true, message: '请选择验收类别', trigger: 'blur' }
        ]
      },
      projectList: []
    }
  },
  created() {
    this.getAllProjectName()
  },
  methods: {
    getAllProjectName() {
      const that = this;
      that.$Spin.show();
      that.commonUtil.ajaxGetUtil('/project/getAllProjectName', {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.projectList = res.data
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 取消操作
    handleCancel(name) {
      this.$refs[name].resetFields();
      this.isPop = false;
    },
    // 确认操作,关系后台数据
    handleConfirm(name) {
      const that = this;
      that.$refs[name].validate((valid) => {
        if (valid) {
          const method = that.isCreate ? '/insertAcceptanceForm' : '/updateAcceptanceOverWrite'
          const form = Object.assign({}, that.form)
          form.enclosureList = that.$refs.uploadFile.getFiles()
          form.pictureList = that.$refs.imgRecord.getImgRecord()
          if (that.isCreate) {
            form.archivesNumber = new Date().getTime()
          }
          that.$Spin.show();
          axiosUtils.ajaxPostUtil(method, form, (res) => {
            that.$Spin.hide();
            if (res.ok) {
              that.isPop = false
              that.$Message.success(res.message);
              that.$emit('refreshList')
            } else {
              that.$Message.error(res.message);
            }
          }, () => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    showModal(id) {
      const that = this;
      that.clearForm();
      if (id) {
        that.$Spin.show();
        axiosUtils.ajaxPostUtil('/selectAcceptanceFormById?acceptanceFormId=' + id, {}, (res) => {
          // 关闭数据加载
          that.$Spin.hide();
          if (res.ok) {
            const form = res.data
            form.basicsOrganizationUnderTakeId = form.basicsOrganizationUnderTake ? Number(form.basicsOrganizationUnderTake.basicsOrganizationId) : null
            form.basicsOrganizationId = form.basicsOrganization ? Number(form.basicsOrganization.basicsOrganizationId) : null
            form.basicsCapitalId = form.basicsCapital ? Number(form.basicsCapital.basicsCapitalId) : null
            that.form = form
            that.$nextTick(() => {
              that.$refs.uploadFile.getFileItems(form.enclosureList)
              that.$refs.imgRecord.setImgRecord(form.pictureList)
              that.isPop = true
            })
          } else {
            that.$Message.error(res.msg);
          }
        }, () => {
          that.$Message.error('网络连接错误');
          // 关闭数据加载
          that.$Spin.hide();
        }, that)
      } else {
        that.isPop = true
      }
    },
    clearForm() {
      this.form = {
        // 项目名称
        projectName: '',
        // 主管单位
        basicsOrganizationId: '',
        // 承担单位
        basicsOrganizationUnderTakeId: '',
        // 资金类别
        basicsCapitalId: '',
        // 验收类别
        acceptanceType: '',
        // 建设位置
        constructionLocation: '',
        // 建设内容
        constructionContents: '',
        //  实际完成
        actualCompletion: '',
        //  检验完成
        inspectionCompleted: '',
        // 情况备注
        remarks: '',
        enclosureList: [],
        pictureList: []
      }
      this.$refs.form.resetFields();
      const uploadFile = this.$refs.uploadFile
      const imgRecord = this.$refs.imgRecord
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
      if (imgRecord) {
        imgRecord.clearImgRecord()
      }
    }
  }
}
</script>



